import * as React from "react";
import { Helmet } from "react-helmet";
import { Container } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import "/src/css/custom.css";
import { render } from "react-dom";
import * as $ from "jquery";

// styles
const pageStyles = {
    color: "#232129",
    fontFamily: "-apple-system, Roboto, sans-serif, serif",
}

const section = {
    padding: "150px 0px 0px"
}

const code = {
    width: "80px",
    fontSize: "18px",
    backgroundColor: "#0000000d",
    border: "2px solid #dcdcdc",
    
}

let project_id = "";
let project = "";
let number = "";
if (typeof window !== 'undefined') {
project_id = localStorage.getItem('project_id');
project = localStorage.getItem('project');
number = localStorage.getItem('number');
}

const fnSendMeDetail = (form) => {
    var name = "BLANK";
    var email = "BLANK";
    var mobile_no = $("#" + form + " input[name=mobile_no]").val();
    var mobile_length = mobile_no.length;
    var msg = "Hello, I am Looking for "+project+", Please send me more details about this project."
    if (mobile_no == "") {
        $("#" + form + " .form_error").html("Please enter mobile no.");
        return false;
    } else if (mobile_length != 10) {
        $("#" + form + " .form_error").html("Please enter valid mobile no.");
        return false;
    } else {
        $("#" + form + " .form_error").html("");
    }

    var lead = {
        mobile: mobile_no,
        project: project,
        project_id: project_id,
        source: "BLANKW" 
     };
   
     $.ajax({
       type: "post",
       url: 'https://mls-crm.manishalifespaces.com/api/add_lead',
       data: lead,
       success: function(data, status, jqXHR) {
         window.location.href = 'https://api.whatsapp.com/send?phone='+number+'&text='+msg;
       },
       error: function(jqXHR, status) {
         console.log(jqXHR);
       }
     });
   
}

// markup
const WhatsappPage = () => {

    return (
        <main style={pageStyles}>
            <Helmet>
                <title>{project}</title>
            </Helmet>

            <Container fluid className="p-0">
                <div class="header">
                    <img src="https://palava-city.in/wp-content/themes/mpd_theme_6_multi_v_6/img/whatsapp-logo.png" />
                    Open WhatsApp
                </div>
                <section style={section}>
                    <div class="container">
                        <form id="whatsappForm" >
                            <div class="text-center">
                                <h2 class="mb-5">Chat on WhatsApp with +91 {number}</h2>
                                <div class="mb-0">
                                <div class="input-group">
                                <select style={code} name="country_code">
                                    <option data-countrycode="IN" value="+91" data-contry_code="91">+91</option>
                                    <option data-countrycode="AF" value="+93" data-contry_code="93">+93</option>
                                    <option data-countrycode="AL" value="+355" data-contry_code="355">+355</option>
                                    <option data-countrycode="DZ" value="+213" data-contry_code="213">+213</option>
                                    <option data-countrycode="AS" value="+1-684" data-contry_code="1-684">+1-684</option>
                                    <option data-countrycode="AD" value="+376" data-contry_code="376">+376</option>
                                    <option data-countrycode="AO" value="+244" data-contry_code="244">+244</option>
                                    <option data-countrycode="AI" value="+1-264" data-contry_code="1-264">+1-264</option>
                                    <option data-countrycode="AQ" value="+672" data-contry_code="672">+672</option>
                                </select>
                              <input type="number" class="form-control" placeholder="Mobile No.*" name="mobile_no" />
                          </div>
                          <div class="input-group">
                          <p class="form_error"></p>
                          </div>
                                </div>
                                <p class="form-text">Hello, I am Looking for {project}, Please send me more details about this project.</p>
                                <hr />
                                <div class="form-group"> <button type="button" class="button" id="WhatsAppSubmit" onClick={() => fnSendMeDetail("whatsappForm")}>Continue&nbsp;to&nbsp;chat</button></div>
                            </div>
                        </form>
                    </div>
                </section>
            </Container>

        </main>

    )

}

export default WhatsappPage
